import { FC, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './ButtonGroup.scss';

// Types
import { TestMetadata } from 'types';

export interface ButtonGroupProps {
  /** Determine if button group should be a segmented set */
  segmented?: boolean;
  /** Determine if button group is solid style */
  solid?: boolean;
  /** Determine if button group is outline style */
  outline?: boolean;
  /* Custom class name(s) for component container */
  className?: string;
  // Test IDs for sections of ButtonGroup
  testMetadata?: TestMetadata;
}

const ButtonGroup: FC<ButtonGroupProps> = ({
  className,
  solid,
  segmented,
  outline,
  children,
  testMetadata,
  ...otherProps
}) => {
  const buttonGroupClassNames = useMemo(
    () =>
      classNames(
        'sb-button-group',
        {
          'sb-button-group_segmented': segmented,
          'sb-button-group_solid': solid,
          'sb-button-group_outline': outline,
        },
        className,
      ),
    [className, outline, segmented, solid],
  );

  return (
    <div {...otherProps} className={buttonGroupClassNames} {...testMetadata}>
      {children}
    </div>
  );
};

export default ButtonGroup;
